var bitmap = null;
export default {
	onShareAppMessage(res) {
	    if (res.from === 'button') {// 来自页面内分享按钮
	      console.log(res.target)
	    }
	},
	data() {
		return {
			showall: false,
			showUpImg:true,
			data_num:2255,
			title: '自定义分享标题',
			path: '/pages/test/test?id=123',
			ins:0,
			imgbox:[{
				pic: "../../static/images/icon12.png",
				img: "../../static/images/icon11.png",
				name:4396,
				num:'listclick'
			},
			{
				pic: "../../static/images/icon10.png",
				name:4396,
				num:'commclick'
			},
			{
				pic: "../../static/images/icon19.png",
				name:4396,
				num:'shareclick'
			}],
			pirve_box: [
				"../../static/images/bar.jpg",
				"../../static/images/bar.jpg",
				"../../static/images/bar.jpg",
				"../../static/images/bar.jpg",
				"../../static/images/bar.jpg",
				"../../static/images/bar.jpg"
			]
		};
	},
	methods: {
		order_le: function(methodsWords,index) {
			this[methodsWords](index)
		},
		listclick: function(index) {
				if(this.ins==index){
					this.ins=-1;
					this.imgbox[0].name++
				}else{
					this.ins=index;
					this.imgbox[0].name--
				}
		},
		commclick:function(){
			uni.navigateTo({
				url:'../page_home/page_home',
			})
		},
		shareclick:function(){
			
		},
		showUp_down:function(){
			console.log(this.showUpImg)
			if(this.showUpImg == true){
				this.showUpImg = !this.showUpImg
			    this.data_num++
			}else{
				this.showUpImg = !this.showUpImg
				this.data_num--
			}
		},
		pirve_image: function() {
			console.log(this.pirve_box)
			uni.previewImage({
				urls: this.pirve_box,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		}
	}
}
